<script>
// Swiper css  js  index.html  script 
// it depends!
import 'swiper/dist/css/swiper.css'
import Swiper from 'swiper'
import { KEY_UID } from 'constants'
import {
  popup_credit_has_apply_once,
  popup_credit_forward_blank,
  pop_credit_city
} from './popups'
import AniamteHeader from '@/widget/animate-header/Index.vue'
const countDownLimit = 60

const toggleMask = (container, on) => {
  container.style.opacity = on ? 0.4 : 1
}

export default {
  props: ['id'],

  components: {
    AniamteHeader
  },

  data () {
    return {
      form: {
        mobile: '',
        username: ''
      },

      cards: [],
      user_reward: '',
      head_tips: '',
      apply_verify_words: '',

      slider: undefined,

      getCodeLock: false,
      getCodeText: '',
      sec: 0,

      // 
      submitLock: false,
      focusOn: '',
      activeIndex: 0,
      currentCard: {},

      // 
      business_url: ''
    }
  },

  watch: {
    'form.mobile': function (val) {
      if (val.length > 11) this.form.mobile = val.substr(0, 11)
    },

    'form.username': function (val) {
    }
  },

  mounted () {
    $api
      .creditTaskDetail(this.id)
      .then(res => res.data && res.data.payload)
      .then((payload) => {
        this.cards = payload.cards
        this.click_apply = payload.click_apply
        this.user_reward = payload.user_reward
        this.head_tips = payload.head_tips
        this.apply_verify_words = payload.apply_verify_words
        this.business_url = payload.business_url
        this.apply_citys = payload.apply_citys
        setTimeout(this.initSwiper, 500)
      })
  },

  methods: {
    onTabClick (index) {
      this.activeIndex = index
    },

    clearMobile () {
      this.form.mobile = ''
      this.$refs.mobile.focus()
    },

    clearUsername () {
      this.form.username = ''
      this.$refs.username.focus()
    },

    focus (type) {
      this.focusOn = type
    },

    getCode () {
      // 
      if (this.getCodeLock) return
      this.getCodeLock = true

      // 
      if (this.sec > 0) return

      // 
      let tips = this.check_phone(this.form.mobile)

      if (tips) {
        setTimeout(() => { this.getCodeLock = false }, 1000)
        $toast.show(tips)
        return
      }

      $api.creditCardsSendCode({phone: this.form.mobile, task_id: this.id})
        .then(() => {
          this.sec = countDownLimit
          this.countDown()
        },
        () => {
          this.getCodeLock = false
        })
    },

    submit () {
      let popup = $popup.fromTemplate(
        popup_credit_forward_blank.template(this.form.mobile),
        popup_credit_forward_blank.options
      )
      popup.show().then((buttonIndex) => {
        if (buttonIndex === 0) {
          if (this.submitLock) return
          this.submitLock = true
          $api.creditCardApplyReward(this.form)
            .then((res) => {
              this.submitLock = false
              this.goToBank()
            })
        } else {
          this.submitLock = false
        }
      })
    },

    apply () {
      let tips = this.check_inputs() ? this.check_inputs() : this.check_phone(this.form.mobile)
      if (tips) {
        $toast.show(tips)
        setTimeout(() => { this.submitLock = false }, 500)
        return
      }

      this.form.task_id = this.id
      $api.creditCardHasApply(this.form)
        .then((res) => {
          if (res.data.err_code === 40005) {
            let popup = $popup.fromTemplate(
              popup_credit_has_apply_once.template,
              popup_credit_has_apply_once.options
            )
            popup.show().then((index) => {
              if (index === 0) {
                $router.back('/credit/list')
              }
              if (index === 1) {
                location.href = this.business_url
              }
            })
          } else {
            this.submit()
          }
        })
    },

    goToBank () {
      let user_id = $storage.get(KEY_UID)
      location.href = this.currentCard.bank_url + '?user_id=' + user_id + '&task_id=' + this.id + '&card_id=' + this.currentCard.id
    },

    initSwiper () {
      let loop = this.cards.length > 2
      if (this.cards.length >= 2) {
        this.slider = new Swiper('#credit-cards', {
          direction: 'horizontal',
          loop: loop,
          speed: 250,
          slidesPerView: 2,
          centeredSlides: true,
          spaceBetween: 20,
          onSlideChangeStart: (swiper) => {
            for (var i = 0; i < swiper.slides.length; i++) {
              if (i === swiper.activeIndex) {
                let slide = swiper.slides[i]
                toggleMask(slide, false)
                this.currentCard = this.cards[parseInt(slide.getAttribute('data-index'))]
              } else {
                toggleMask(swiper.slides[i], true)
              }
            }
          }
        })
      }

      this.currentCard = this.cards[0]
      this.swiperCreated = true
    },

    countDown () {
      if (this.sec) {
        this.sec--
        this.getCodeText = this.sec ? '(' + this.sec + 's)' : ''
        setTimeout(() => {
          this.countDown()
        }, 1000)
      } else {
        this.getCodeLock = false
      }
    },

    check_inputs () {
      let tips = ''
      let regx = new RegExp('\\s')
      if (!this.form.username) {
        tips = ''
      } else if (regx.test(this.form.username)) {
        tips = ''
      }
      return tips
    },

    check_phone (phone) {
      let tips = ''
      if (!phone) {
        tips = ''
      } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
        tips = ''
      }

      return tips
    },

    goToList () {
      $router.back('/credit/list')
    },

    showPop () {
      let pop = $popup.fromTemplate(
        pop_credit_city.template(this.apply_citys),
        pop_credit_city.options
      )
      pop.show()
    }
  }
}
</script>



